.enter-api-key-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;

    .form-container {
        background-color: red;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;

        form {
            width: 100%;

            input {
                width: 80%
            }

            button {
                width: 20%
            }
        }
    }
}