body {
    padding: 0px;
    margin: 0;
}

.post-editor {
    min-width: 100vw;
    min-height: 100vh;
    background-color: white;
    margin: 0px !important;
    padding: 0px !important;


    .post-image-editor {
        position: fixed;
        top: calc(-1 * 100vh);
        z-index: 999;
        background-color: white;
    }

    .post-container {
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 100%;
        margin: 0px !important;
        padding: 0px !important;

        .post-row {

            .post-column-left {
                margin: 0px !important;
                padding: 0px !important;
                border-left: 2px solid black;
                height: 100%;

                .floating-info {
                    position: fixed;
                    left: 1rem;
                    font-size: 30px;
                    color: gray;
                    z-index: 100 !important;
                }

                .border {
                    position: fixed;
                    left: -2px !important;
                    top: -2px !important;
                    height: 52px;
                    width: calc(50% - 2px);
                    z-index: 99;
                    background-color: white;
                }

                .inputs {
                    margin-top: 40px;
                    width: 100%;

                    label {
                        margin-left: 20px;
                        font-family: sans-serif;
                        font-weight: bold;
                        font-size: 18px;
                        margin-top: 20px;
                    }

                    input {
                        margin: 10px 20px;
                        width: calc(100% - 40px);
                    }

                    select {
                        margin: 10px 20px;
                        width: calc(100% - 40px);
                    }

                    button {
                        margin: 20px 20px;
                        width: calc(100% - 40px);
                    }
                }
            }

            .post-column-right {
                margin: 0px !important;
                padding: 0px !important;
                border-right: 2px solid black;
                height: 100%;

                .floating-info {
                    position: fixed;
                    right: 1rem;
                    font-size: 30px;
                    color: gray;
                    z-index: 100 !important;
                }

                .border {
                    position: fixed;
                    right: -2px !important;
                    top: -2px !important;
                    height: 52px;
                    width: calc(50% - 2px);
                    z-index: 99;
                    background-color: white;
                }

                .textarea {
                    font-size: 20px;
                    font-family: sans-serif;
                    margin-top: 50px;
                    padding: 10px 30px 0px 30px;
                    display: flex;
                    width: 100%;
                    height: 100%;
                    border: 1px solid lightgray !important;
                    outline: none;
                    min-width: 100% !important;
                    min-height: 100vh !important;
                }
            }


        }
    }

    .toggler {
        cursor: pointer;
        text-align: center;
        position: fixed;
        left: 50%;
        top: 50%;
        bottom: 0px;
        transform: translateX(-50%) translateY(-50%);
        width: 80px;
        height: 80px;
        border-radius: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
    }

    .b-red {
        background-color: red;
    }

    .b-green {
        background-color: green;
    }

    .popup-toggler {
        position: fixed;
        width: 180px;
        height: 50px;
        background-color: red;
        z-index: 1300;
        top: 20px;
        right: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        color: white;
        font-size: 19px;
        border: 3px solid black;
        border-radius: 5px;
        cursor: pointer;
    }

    .popup-saver {
        position: fixed;
        width: 180px;
        height: 50px;
        background-color: green;
        z-index: 1300;
        top: 80px;
        right: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        color: white;
        font-size: 19px;
        border: 3px solid black;
        border-radius: 5px;
        cursor: pointer;
    }
}

.photo-url-popup {
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 100px;
    position: fixed;
    z-index: 1200;
    width: 100vw;
    height: 100vh;
    overflow-y: scroll;
    top: 0px;
    left: 0px;
    background-color: white;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;

    /* Firefox */
    &::-webkit-scrollbar {
        display: none;
    }

    .photo-url-label {
        margin-top: 37.5px;
        margin-right: 20px;
    }

    .photo-input {
        margin-top: 30px;
        width: calc(100vw - 400px);
    }

    .add-input {
        margin-top: 30px;
        margin-left: 50px;
        height: 40px;
    }


    .photos {
        * {
            font-family: sans-serif !important;
        }

        div {
            margin-top: 5px;
        }

        padding:50px;
        border:3px solid black;
        margin:100px 0px 0px 0px !important;
        font-weight: bold;
    }

}