.container {
    .login-screen {
        display: flex;
        justify-content: center;
        width: 100%;
        min-height: 100vh;
        background-color: #ffffff;
        align-items: center;

        .Auth-form {
            width: 320px;

            .login-submit {
                outline: none !important;
                border: none !important;
                box-shadow: none !important;

                &:focus {
                    outline: none !important;
                    border: none !important;
                    box-shadow: none !important;
                }

                &:hover {
                    outline: none !important;
                    border: none !important;
                    box-shadow: none !important;
                }
            }
        }
    }
}