.posts-container {
    margin-bottom: 70px;

    .header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: start;
        margin-top: 40px !important;
        position: relative;
        font-size: 23px;

        h1 {
            padding-left: 30px;
            font-weight: bold;
            font-family: sans-serif;
        }

        a {
            color: red;
            font-weight: bold;
            text-decoration: none;
        }

        .add-post {
            position: absolute;
            right: 40px;
            top: 0px;
        }
    }

    .post-search {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 30px;
        padding: 0px 40px;


        .search-input {
            width: 80%;
            height: 40px;
            border: 1px solid #ccc;
            border-radius: 5px;
            padding: 0px 10px;
            font-size: 16px;
            outline: none;
            margin-right: 20px;
        }

        .search-button {
            width: 20%;
            height: 40px;
            border: 1px solid #ccc;
            border-radius: 5px;
            padding: 0px 10px;
            font-size: 16px;
            outline: none;
            background-color: #ccc;
            cursor: pointer;
        }
    }

    .posts {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        .post {
            cursor: pointer;
            position: relative;
            margin: 10px;
            margin-top: 40px !important;
            width: calc(33.3333% - 40px);
            min-height: 390px !important;

            .post-cover {
                height: 200px !important;
                object-fit: cover;
            }

            .post-title {
                font-weight: bold;
                padding: 0px 10px;
                margin-top: 10px;
                font-family: sans-serif;
                font-size: 18px;
                -webkit-box-orient: vertical;
                display: -webkit-box;
                -webkit-line-clamp: 4;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: normal;
            }

            .post-link {
                position: absolute;
                bottom: 10px;
                left: 10px;

                a {
                    font-weight: bold;
                }
            }
        }
    }
}